var React = require('react/addons');
var Modal=require('react-bootstrap/lib/Modal');
var Input=require('react-bootstrap/lib/Input');
var Button=require('react-bootstrap/lib/Button');
var If=require('../commons/if.jsx')
var IndicatorsFinderStore=require('../../stores/indicatorsFinderStore.js');
var Reflux = require('reflux');
var Actions=require('../../actions/saveActions.js');
var CheckBox=require('../commons/customCheckbox.jsx')
var Store=require('../../stores/saveStore.js');

var _=require('lodash');

var Tags= React.createClass({displayName: "Tags",
	_update:function(event){
		this.props.onUpdate(event.target.value);
	},

	render:function() {
		return (React.createElement(Input, {ref: "tags", className: "form-control taginput", type: "text", defaultValue: this.props.value, onBlur: this._update}))
	}

})


module.exports = React.createClass({displayName: "exports",


	mixins: [Reflux.connect(Store,"store")],

	save:function(){
	//TODO add validations
		var description=this.refs.description.getValue();
		var title=this.refs.title.getValue();
		_.assign(this.state,{'title':title,'description':description});
		if(this.state.store.key=='save'){
			Actions.saveMap(this.state);
		} else if(this.state.store.key=='update') {
			Actions.updateMap(this.state.store.id, this.state);
		}
	},

	close:function(){
		Actions.hideModal();
	},

	open:function(){
		Actions.showModal()
	},

	getInitialState:function(){
		return {title:'', description:''}
	},

	updateTags:function(event){
		_.assign(this.state,{'tags':event});
	},

	getTags:function(event){
		var self = this;
		var tags;
		if(self.state.store.id && self.state.store.showModal){
			var map = _.find(this.state.store.maps, function(l){return l._id==self.state.store.id});
			tags = map.tags;
		}
		return tags;
	},

	componentDidUpdate:function(){
		var self = this;
		if(self.state.store.id && self.state.store.showModal){
			var map = _.find(this.state.store.maps, function(l){return l._id==self.state.store.id});
			self.refs.title.getInputDOMNode().value = map.title;
			self.refs.description.getInputDOMNode().value = map.description;
		}

	},

	render:function() {
		var showModal=this.state.store.showModal || false;
		return (
			React.createElement("div", {className: "save-map-trigger"}, 
			React.createElement(Modal, React.__spread({className: "dialog-save-map"},  this.props, {bsSize: "large", "aria-labelledby": "contained-modal-title-lg", 
			 show: showModal, onHide: this.close}), 
				React.createElement(Modal.Header, null, 
					React.createElement(Modal.Title, null, 
						React.createElement("i", {className: "fa fa-folder-open"}), React.createElement(Message, {message: "savemap.savemapdialogtitle"})
					), 
					React.createElement("a", {className: "close-dialog", href: "#", onClick: this.close}, 
					React.createElement("i", {className: "fa fa-times-circle-o"}))
				), 
				React.createElement(Modal.Body, null, 

				React.createElement("div", {className: "blue-panel"}, 
					React.createElement(Input, {name: "title", ref: "title", className: "form-control", type: "text", placeholder: i18n.t('savemap.savemaptitle'), maxlength: "100", addonAfter: "*"}), 
					React.createElement(Input, {type: "textarea", name: "description", ref: "description", className: "form-control", rows: "3", placeholder: i18n.t('savemap.savemapdescription'), maxlength: "300", addonAfter: "*"})
				), 

					React.createElement("div", {className: "plain-panel"}, 
					React.createElement("h4", {className: "modal-title"}, React.createElement(Message, {message: "savemap.savemaptags"})), 
					React.createElement(Tags, {onUpdate: this.updateTags, value: this.getTags()}), 
					React.createElement("span", null, " ", React.createElement(Message, {message: "savemap.tagshelptext"}))
					), 
					React.createElement("div", {className: "plain-panel"}, React.createElement(Message, {message: "savemap.mandatoryFields"}), 
						React.createElement(If, {condition: this.state.store.errorMsg}, 
							React.createElement(Message, {message: this.state.store.errorMsg})
						)
					)
				), 
				React.createElement(Modal.Footer, null, 
					React.createElement(Button, {className: "btn btn-apply pull-right", onClick: this.save.bind(this)}, i18n.t('savemap.savebutton')), 
					React.createElement(Button, {className: "pull-right", onClick: this.close.bind(this)}, i18n.t('savemap.closebutton'))
				)
			)
			)
			);
}
});
